import { asyncRouterMap, constantRouterMap, localComponents } from '@/router'
import { RequestHelper } from '@/assets/js/RequestHelper'


/**
 * meta.role
 * @param roles
 * @param route
 */
function hasPermission(roles, route) {
    if (route.meta && route.meta.role) {
        return roles.some(role => route.meta.role.indexOf(role) >= 0)
    } else {
        return true
    }
}

/**
 * 
 * @param asyncRouterMap
 * @param roles
 */
function filterAsyncRouter(asyncRouterMap, roles) {
    const accessedRouters = asyncRouterMap.filter(route => {
        if (hasPermission(roles, route)) {
            if (route.children && route.children.length) {
                route.children = filterAsyncRouter(route.children, roles)
            }
            return true
        }
        return false
    })
    return accessedRouters
}

/**
 *    
 */
function filterLocalRouter(routes) {
    for (let route of routes) {
        setComponentByLocalComponent(route);
    }
    console.log("==========");
    console.log(routes);
}

/**
 *   
 */
function setComponentByLocalComponent(route) {
    if (!route) {
        return;
    }
    let component = getComponentByName(route.name);
    if (component) {
        route.component = component;
    }
    if (!route.children || route.children.length === 0) {
        return;
    }
    for (let child of route.children) {
        setComponentByLocalComponent(child);
    }
}

/**
 * 
 */
function getComponentByName(desName) {
    if (!desName) {
        // desName
        return null;
    }
    for (let localComponent of localComponents) {
        if (desName === localComponent.name) {
            // require default
            return localComponent.component.default;
        }
    }
    return null;
}

const permission = {
    state: {
        routers: constantRouterMap,
        addRouters: []
    },
    mutations: {
        SET_ROUTERS: (state, routers) => {
            state.addRouters = routers
            state.routers = constantRouterMap.concat(routers)
        }
    },
    actions: {
        GenerateRoutes({ commit }, data) {
            return new Promise(resolve => {
                //    

                // const { roles } = data
                // let accessedRouters
                // // admin
                // if (roles.indexOf('admin') >= 0) {
                //     accessedRouters = asyncRouterMap
                // } else {
                //     accessedRouters = filterAsyncRouter(asyncRouterMap, roles)
                // }
                // console.log(accessedRouters);
                // commit('SET_ROUTERS', accessedRouters)
                // resolve()

                // 
                RequestHelper.getMenuFromWeb({
                    onResult(isSuccess, data) {
                        if (isSuccess) {
                            let routes = data.body;
                            filterLocalRouter(routes);
                            commit('SET_ROUTERS', routes)
                            resolve()
                        }
                    }
                })
            })
        }
    }
}

export default permission


