<template>
    <div class="main-container gray-bg">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="100px">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.userId" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.phone" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-date-picker v-model="form.openTime" type="datetimerange" range-separator="-" start-placeholder="" end-placeholder="">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="">
                        <el-date-picker v-model="form.refundTime" type="datetimerange" range-separator="-" start-placeholder="" end-placeholder="">
                        </el-date-picker>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-select v-model="form.userType" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-select v-model="form.sendStatus" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.sms" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.result" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button @click="$store.commit('SET_SHOW_SENDMSGOFFLINE',true)"></el-button>
                        <el-button style="float:right;" type="primary">&nbsp;&nbsp;</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div style="background-color:#E6F3FC; height:36px; line-height:36px; margin-top:5px;margin-bottom:5px; font-weight:600;">
            &nbsp;&nbsp;&nbsp;
            <i class="el-icon-info" style="color:#108EE9;font-size:18px; line-height:36px;"></i>
            <span style="margin-left:4%;"></span>
            <span style="color:#108EE9;">{{1234}}</span>
            <span style="margin-left:5%;"></span>
            <span style="color:#108EE9;">{{12}}</span>
        </div>

        <el-table :data="tableData" v-loading="isLoading" style="width: 100%">
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.type}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.openTime}}</span>
                </template>
            </el-table-column>

            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.sendTime}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.sendStatus }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.smsContent }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <send-msg-off-line></send-msg-off-line>
    </div>
</template>

<script>
import SendMsgOffLine from "@/business/smsPreview/view/SendMsgOffLine";
export default {
    components: { SendMsgOffLine },
    props: {},
    data() {
        return {
            form: {},
            tableData: [],
            pageInfo: {
                currentSize: 20,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {
        this.getBaseProductListFromWeb();
    },

    methods: {
        /**
         * 
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                baseProduct: this.formInline,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.body;
        },
        /**
         * page 
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 
         */
        handleSizeChange(val) {
            console.log(` ${val} `);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 
         */
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         *  pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        searchDialRecord() {}
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



