<template>
    <div>
        <div>
            <h3 style="float:left;">
                <span style="color:#f00;font-weight:700;margin-right:6px;"></span>
            </h3>
            <div style="width:60%;background-color:#E6F3FC; float:right; height:30px;">
                <span style="line-height:30px;margin-left:10px;">
                    <span style="color:#108EE9;">{{activity.name}}</span>
                </span>
                <el-button size="mini" style="height:28px;float:right;margin:1px 10px;"></el-button>
            </div>
        </div>
        <div style="clear:left;"></div>
        <br/>
        <el-form size="mini" class="demo-form-inline">
            <el-col :span="6">
                <el-form-item>
                </el-form-item>
            </el-col>
            <el-col :span="14">
                <el-form-item label="">
                    <el-date-picker style="width:70%;" v-model="time" type="daterange" align="right" unlink-panels range-separator="-" start-placeholder="" end-placeholder="">
                    </el-date-picker>
                </el-form-item>
            </el-col>
            <el-col :span="4">
                <el-form-item>
                    <el-button size="mini" type="primary">&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
                </el-form-item>
            </el-col>
        </el-form>
        <div style="clear:left; margin-bottom:10px;">
            <el-button size="mini">&nbsp;&nbsp;&nbsp;&nbsp;</el-button>
        </div>
        <div>
            <div id="info" style="clear:both;height:68px;background-color:#E6F3FC; line-height:34px; ">
            <el-col :span="4">
                <i class="el-icon-info" style="display:inline-block;vertical-align:middle;color:#108EE9;margin-left:10px;font-size:20px;line-height:68px;"></i><span style="display:inline-block;vertical-align:middle;font-weight:600;"></span>
            </el-col>
             <el-col :span="20">
                 <el-row>
                    <el-col :span="8">()<span>1234</span></el-col>
                    <el-col :span="5"><span>1234</span></el-col>
                    <el-col :span="5"><span>1234</span></el-col>
                    <el-col :span="6"><span>1234</span></el-col>
                </el-row>
                <el-row>
                    <el-col :span="4"><span>1234</span></el-col>
                    <el-col :span="5"><span>1234</span></el-col>
                    <el-col :span="5"><span>1234</span></el-col>
                    <el-col :span="5"><span>1234</span></el-col>
                    <el-col :span="5"><span>1234</span></el-col>
                </el-row>
             </el-col>
        </div>
            <el-table :data="productList" style="width: 100%">
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.title }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="()" width="130" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payType}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPiont}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPrice }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" width="90" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.orderCycleCount }}</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
        <div style="margin-top:30px;">
            <p>
                <span style="color:#108EE9;">*</span> <br/> &nbsp;&nbsp;&nbsp;1=/
                <br/> &nbsp;&nbsp;&nbsp;2
                <br/> &nbsp;&nbsp;&nbsp;31130
            </p>
        </div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            time: "",
            activity: { type: Object },
            productList: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },
    created() {
    },
    filters: {
        // 
    },
    methods: {
        /**
         * page 
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 
         */
        handleSizeChange(val) {
            console.log(` ${val} `);
            this.pageInfo.currentSize = val;
            this.getActivityListFromWeb();
        },
        /**
         * 
         */
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.pageInfo.number = val;
            this.getActivityListFromWeb();
        },
        /**
         *  pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#info span{
    color:#108EE9;
}
</style>



