<template>
    <div style="background-color:white; padding:20px 10px;">
        <div>
            <el-table :data="productList" style="width: 100%">
                <el-table-column width="50" lable="">
                    <template slot-scope="scope">
                        <el-radio :label="scope.row.id" v-model="templateRadio" @change.native="getTemplateRow(scope.$index,scope.row)">&nbsp;</el-radio>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.title }}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payType | payTypeFilter}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPoint}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" show-overflow-tooltip>
                    <template slot-scope="scope">
                        <span>{{ scope.row.payPrice }}</span>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <br>
        <div>
            <div v-if="subscriber" style="width:60%;float:left; margin-bottom:20px;">
                <table style="border:solid 1px rgb(233,233,233);width:100%;">
                    <tr>
                        <td class="colum"></td>
                        <td>{{subscriber.userName}}</td>
                        <td class="colum"></td>
                        <td>{{subscriber.userId}}</td>
                    </tr>
                    <tr>
                        <td class="colum"></td>
                        <td>{{subscriber.deviceId}}</td>
                        <td class="colum"></td>
                        <td>{{subscriber.point}}</td>
                    </tr>
                    <tr>
                        <td class="colum">/</td>
                        <td>{{subscriber.phone}}
                            <el-button type="text" id="phone" @click="togglePhone"></el-button>
                        </td>
                        <td class="colum"></td>
                        <td>{{subscriber.address}}
                            <el-button type="text" id="address" @click="toggleAddress"></el-button>
                        </td>
                    </tr>
                </table>
            </div>
            <div v-if="!subscriber" style="width:60%;float:left; margin-bottom:20px;">
                <h2 style="color:red;"></h2>
            </div>
            <div id="result" style="width:35%;float:right; height:500px;">
                <el-form :model="order" ref="resultForm" :rules="rules" label-position="left" size="mini">
                    <el-form-item label="">
                        <el-radio-group v-model="order.result">
                            <el-radio label="12" style="color:#66CC00;"></el-radio>
                            <el-radio label="1" style="color:red;"></el-radio>
                        </el-radio-group>
                        <!--  -->
                        <div v-if="order.result==12">
                            <div style="clear:left;margin-bottom:30px;">
                                <el-form-item label="" prop="userPhone">
                                    <el-input v-model="order.userPhone" placeholder=""></el-input>
                                </el-form-item>
                                <!--  -->
                                <el-form-item v-if="checkedProduct && checkedProduct.type==0" label="">
                                    <el-input-number v-model="order.cycleCount" :min="1" :max="maxCount" label=""></el-input-number>
                                </el-form-item>
                            </div>
                        </div>
                        <div v-if="order.result==1">
                            <el-radio-group v-model="order.falseType">
                                <div style="margin-bottom:30px;">
                                    <el-radio label="1" style="color:#555; margin-right:20px;"></el-radio>
                                    <el-select v-if="order.falseType==1" v-model="order.failedReason" placeholder="" style="width:40%;margin-right:20px;">
                                        <el-option v-for="failReason in failReasonList" :label="failReason.text" :value="failReason.value" :key="failReason.value"></el-option>
                                    </el-select>
                                    <el-select v-if="order.falseType != 1" v-model="order.failedReason" disabled placeholder="" style="width:40%;margin-right:20px;">
                                    </el-select>
                                </div>
                                <div style="margin-bottom:30px;">
                                    <el-radio label="6" style="color:#555; margin-right:20px;">&nbsp;&nbsp;&nbsp;&nbsp;</el-radio>
                                    <el-select v-if="order.falseType==6" v-model="order.willingType" placeholder="" style="width:40%;margin-right:20px;">
                                        <el-option label="" value="1"></el-option>
                                        <el-option label="" value="2"></el-option>
                                        <el-option label="" value="3"></el-option>
                                    </el-select>
                                    <el-select v-if="order.falseType != 6" v-model="order.willingType" disabled placeholder="" style="width:40%;margin-right:20px;">
                                    </el-select>
                                </div>
                                <div v-if="order.falseType==6 && order.willingType==1" style="float:right; margin-top:-20px;">
                                    <el-form-item label="">
                                        <el-date-picker v-model="order.yuYueTime" @change="formatDate" style="width:85%;" type="datetime" placeholder="">
                                        </el-date-picker>
                                    </el-form-item>
                                </div>
                                <div>
                                    <el-radio label="2" style="color:#555;"></el-radio>
                                </div>
                            </el-radio-group>
                        </div>
                    </el-form-item>
                    <el-form-item>
                        <el-button v-if="subscriber && order.result" style="float:right;" type="primary" @click="saveRecord()"></el-button>
                        <el-button v-if="!order.result || !subscriber" disabled style="float:right;" type="primary" @click="saveRecord()"></el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div><br>
        <div id="dial" style="width:60%;float:left;">
            
            <el-input size="mini" v-model="subscriber.phone" style="width:40%;margin-bottom:10px;"></el-input>
            <br/> 
            <el-button size="small" type="primary"></el-button>
            <el-button size="small">IVR</el-button>
            <el-button size="small"></el-button>
            <el-button size="small"></el-button>
        </div><br>
        <div style="float:left;width:60%; margin-top:30px;">
            <table id="table2" style="border:solid 1px rgb(233,233,233);">
                <tr>
                    <td class="colum"></td>
                    <td class="colum1"></td>
                </tr>
                <tr>
                    <td class="colum"></td>
                    <td class="colum1"></td>
                </tr>
                <tr>
                    <td class="colum"></td>
                    <td class="colum1"></td>
                </tr>
                <tr>
                    <td class="colum"></td>
                    <td class="colum1">{{activity.script}}</td>
                </tr>
            </table>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
import { Loading } from "element-ui";
import Constant from "@/assets/js/Constant";
export default {
    components: {},
    props: {
        activity: { type: Object }
    },
    data() {
        return {
            templateRadio: 1,
            failReasonList: [],
            order: {
                result: "", //  12:; 1:
                failedReason: "", //
                userPhone: "", // 
                cycleCount: "", // 
                falseType: "", // 1: 6: 2:
                willingType: "", //
                yuYueTime: "", // 
                appoDate: ""
            },
            maxCount: "", // 
            activityId: "", // id
            checkedProduct: null, // 
            productList: [
                {
                    desc: "string",
                    exclusionPID: "string",
                    id: 0,
                    maxPoint: 0,
                    minPoint: 0,
                    name: "string",
                    orderCount: 0,
                    orderCycleCount: 0,
                    origPrice: 0,
                    payPoint: 2,
                    payPrice: 1,
                    payType: 2,
                    pointProductId: "string",
                    productCode: "string",
                    status: 0,
                    title: "-1804",
                    type: 0
                }
            ],
            subscriber: {},
            rules: {
                userPhone: [
                    {
                        required: false,
                        pattern: /^1[34578]\d{9}$/,
                        message: "",
                        trigger: "blur"
                    }
                ]
            }
        };
    },
    created() {
        this.failReasonList = Constant.failReasonList;
        this.getProductsByActivityIdFromWeb();
    },
    computed: {},
    methods: {
        /** */
        getTemplateRow(index, row) {
            this.checkedProduct = row;
        },
        /** */
        getProductsByActivityIdFromWeb() {
            let that = this;
            this.$activityRequest.getProductsByActivityId({
                activityId: this.activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.productList = data.result;
                    } else {
                    }
                }
            });
        },
        /** */
        nextUser() {
            let that = this;
            let loadingInstance = Loading.service({
                fullscreen: true,
                text: ",...",
                spinner: "el-icon-loading",
                background: "rgba(0,0,0,0.8)"
            });
            this.$activityRequest.nextUser({
                activityId: this.activity.id,
                opId: 1,
                //opId: window.parent.userInfo.userId,
                onResult(isSuccess, data) {
                    // 
                    //this.$emit("isLoadingChange", false);
                    if (isSuccess) {
                        if (data.status == 200 && data.result) {
                            that.subscriber = data.result;
                        } else if (data.status == 200 && !data.result) {
                            that.$message({
                                type: "warning",
                                message: ""
                            });
                            //that.$store.commit("SET_SHOW_CALLPAGE", false);
                            that.subscriber = null;
                        } else {
                            that.$message({
                                type: "error",
                                message: ""
                            });
                        }
                    } else {
                        that.$message({
                            type: "error",
                            message: ""
                        });
                    }
                    loadingInstance.close();
                }
            });
        },
        /** */
        saveRecord() {
            var that = this;
            var callRecord = {
                activityName: this.activity.name,
                activityId: this.activity.id,
                userId: this.subscriber.userId,
                userName: this.subscriber.userName,
                opId: 1,
                // opId: document.parent.userInfo.userId,
                opName: ""
            };
            if (!this.order.result) {
                return;
            }
            if (this.order.result == 12) {
                callRecord.result = 12;
                if (this.checkedProduct == null) {
                    this.$message({
                        type: "error",
                        message: ""
                    });
                    return;
                } else {
                    callRecord.productId = this.checkedProduct.id;
                    callRecord.productName = this.checkedProduct.name;
                    callRecord.orderCycleCount = this.checkedProduct.orderCycleCount;
                    callRecord.payType = this.checkedProduct.payType;
                    callRecord.payPoint = this.checkedProduct.payPoint;
                    callRecord.payPrice = this.checkedProduct.payPrice;
                    callRecord.orderPhone = this.order.userPhone;
                }
            } else if (this.order.result == 1) {
                if (this.order.falseType == "") {
                    this.$message({
                        type: "error",
                        message: ""
                    });
                    return;
                } else if (this.order.falseType == 1) {
                    // 
                    callRecord.result = 1;
                    if (this.order.failedReason == "") {
                        this.$message({
                            type: "error",
                            message: ""
                        });
                        return;
                    } else {
                        callRecord.failedReason = this.order.failedReason;
                    }
                } else if (this.order.falseType == 6) {
                    // 
                    callRecord.result = 6;
                    if (this.order.willingType == "") {
                        this.$message({
                            type: "error",
                            message: ""
                        });
                        return;
                    } else if (this.order.willingType == 1) {
                        if (this.order.yuYueTime == "") {
                            this.$message({
                                type: "error",
                                message: ""
                            });
                            return;
                        } else {
                            callRecord.willType = this.order.willingType;
                            callRecord.appoDate = this.order.appoDate;
                        }
                    } else {
                        callRecord.willType = this.order.willingType;
                    }
                } else {
                    callRecord.result = 2;
                }
            }
            this.$refs["resultForm"].validate(valid => {
                if (valid) {
                    let loadingInstance = Loading.service({
                        fullscreen: true,
                        text: ",...",
                        spinner: "el-icon-loading",
                        background: "rgba(0,0,0,0.8)"
                    });
                    this.$callRecordRequest.addCallRecord({
                        callRecord: callRecord,
                        onResult(isSuccess, data) {
                            if (isSuccess) {
                                if (data.status == 200) {
                                    that.$emit("refreshMyActivity");
                                    that.$message({
                                        type: "success",
                                        message: ""
                                    });
                                    that.nextUser();
                                } else {
                                    that.$message({
                                        type: "error",
                                        message: ""
                                    });
                                }
                            } else {
                                that.$message({
                                    type: "error",
                                    message: ""
                                });
                            }
                            loadingInstance.close();
                        }
                    });
                } else {
                    that.$message({
                        type: "error",
                        message: ""
                    });
                }
            });
        },
        togglePhone() {
            var content = $("#phone").text();
            if (content == "") {
                $("#phone").text("");
            } else {
                $("#phone").text("");
            }
        },
        toggleAddress() {
            var content = $("#address").text();
            if (content == "") {
                $("#address").text("");
            } else {
                $("#address").text("");
            }
        },
        formatDate(val) {
            if (val) {
                const dateMat = new Date(val);
                const year = dateMat.getFullYear();
                const month = dateMat.getMonth() + 1;
                const day = dateMat.getDate();
                const hh = dateMat.getHours();
                const mm = dateMat.getMinutes();
                const ss = dateMat.getSeconds();
                const timeFormat =
                    year +
                    "-" +
                    month +
                    "-" +
                    day +
                    " " +
                    hh +
                    ":" +
                    mm +
                    ":" +
                    ss;
                this.order.appoDate = timeFormat;
            }
            console.log(this.order.appoDate);
        }
    },
    mounted() {
        this.nextUser();
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    },
    watch: {
        activity(nawActivity, old) {
            this.getProductsByActivityIdFromWeb();
            this.nextUser();
        },
        checkedProduct(current, old) {
            // 
            if (current.type === 0) {
                this.order.cycleCount = Math.floor(
                    this.subscriber.point / current.payPoint
                );
                this.maxCount = Math.floor(
                    this.subscriber.point / current.payPoint
                );
            }
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
td {
    text-align: center;
    border: 1px solid #cccccc;
    height: auto;
}
.colum {
    padding: 6px;
    font: 1.2em sans-serif;
    font-weight: 500;
    color: black;
    width: 20%;
}
#dial > .el-button {
    margin-left: -5px;
}
.colum1 {
    width: 80%;
}
</style>



