                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label=" " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label=" " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label=" " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="A " align="center " show-overflow-tooltip>
                <template slot-scope="scope ">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="B " align="center " show-overflow-tooltip>
