<template>
    <div class="main-container gray-bg">
        <el-form ref="form" size="mini" :model="form" label-position="right" label-width="120px">
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.userId" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.userName" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-select v-model="form.status" placeholder="">
                            <el-option label="" value="shanghai"></el-option>
                            <el-option label="" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>

            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.phone" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.deviceId" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="CRMID">
                        <el-input v-model="form.crmId" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-row :gutter="10">
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.reason" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item label="">
                        <el-input v-model="form.status" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="8">
                    <el-form-item>
                        <el-button type="primary">&nbsp;&nbsp;</el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
        <div style="margin-bottom:8px;">
            <el-upload style="display:inline-block;" class="upload-demo" accept=".xlsx" action="https://jsonplaceholder.typicode.com/posts/">
                <el-button size="mini"></el-button>
            </el-upload>
            <el-button size="mini" type="primary" style="margin-left:5%;"></el-button>
        </div>

        <el-table :data="tableData" v-loading="isLoading" style="width: 100%">
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userId }}</span>
                </template>
            </el-table-column>
            <!---->
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userType}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.userName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.phone}}</span>
                </template>
            </el-table-column>

            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.deviceId}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.point }}</span>
                </template>
            </el-table-column>
            <el-table-column label="CRMID" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.smsContent }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <!---->
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <!---->
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.result }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <el-button type="text"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
export default {
    components: {},
    props: {},
    data() {
        return {
            form: {},
            tableData: [],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {},

    methods: {
        /**
         * 
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                baseProduct: this.formInline,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.body;
        },
        /**
         * page 
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 
         */
        handleSizeChange(val) {
            console.log(` ${val} `);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 
         */
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         *  pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        searchDialRecord() {}
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



