const orderAndRefund = {
    state: {
        showApplyOpenDlg: false, // 
        showOrderApplyDetail: false, // 
        showSearchUserDlg: false, // 
        showRefundDetailDlg:false, // 
    },
    mutations: {
        SET_SHOW_APPLYOPENDLG(state, data) {
            state.showApplyOpenDlg = data;
        },
        SET_SHOW_ORDERAPPLYDETAIL(state, data) {
            state.showOrderApplyDetail = data;
        },
        SET_SHOW_SEARCHUSERDLG(state, data) {
            state.showSearchUserDlg = data;
        },
        SET_SHOW_REFUNDDETAILDLG(state, data) {
            state.showRefundDetailDlg = data;
        }
    },
    actions: {}
};
export default orderAndRefund;


