<template>
    <div style="width:100%; border:solid 1px #CCC">
        <div style="margin-top: 20px">
            <div style="border-bottom:solid 1px #ccc">
                <h3 style="display:inline-block;vertical-alige:middle; margin-left:3%;"></h3>
                <el-select size="mini" v-model="currentActivity" style="margin:0 14px;width:50%;">
                    <el-option v-for="activity in activityList" :key="activity.id" :value="activity">
                        {{activity.name}}
                    </el-option>
                </el-select>
                <el-button type="text" style="line-height:1px;"></el-button>
            </div>
            <el-radio-group v-model="checkedTeam" size="mini" style="margin-top:10px;">
                <el-radio-button :label="0" checked></el-radio-button>
                <el-radio-button v-for="team in teams" :key="team.teamName" :label="team.teamName"></el-radio-button>
            </el-radio-group>
            <el-table :data="currentTop5" style="width: 100%">
                <el-table-column prop="rank" label="" align="center" width="50">
                </el-table-column>
                <el-table-column prop="opName" label="" align="center" width="50">
                </el-table-column>
                <el-table-column prop="orderCount" label="" align="center" width="70">
                </el-table-column>
                <el-table-column prop="name" label="" align="center" width="90">
                </el-table-column>
                <el-table-column prop="address" label="" align="center" width="70">
                </el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            activityList: [],
            currentActivity: {},
            checkedTeam: "",
            currentTop5: [
                { rank: 1, opName: "", orderCount: 199 },
                { rank: 2, opName: "", orderCount: 121 },
                { rank: 3, opName: "", orderCount: 137 },
                { rank: 4, opName: "", orderCount: 158 },
                { rank: 5, opName: "", orderCount: 101 }
            ],
            teams: [
                {
                    teamName: "",
                    members: [
                        { opId: 3001, opName: "1" },
                        { opId: 3002, opName: "2" }
                    ]
                },
                {
                    teamName: "",
                    members: [
                        { opId: 4001, opName: "1" },
                        { opId: 4002, opName: "2" }
                    ]
                }
            ]
        };
    }
};
</script>



