<template>
    <el-dialog title="" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form ref="modProduct" :model="product" :rules="rules1" label-width="180px" size="mini" label-position="right" class="demo-dynamic">
            <el-row type="flex" class="row-bg">
                <el-col :span="12">
                    <el-form-item label="ID" class="item" prop="productCode">
                        <el-input v-model="product.productCode" disabled placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="name">
                        <el-input v-model="product.name" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" class="item" prop="maxPoint">
                        <el-input v-model="product.maxPoint" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="/" prop="payPrice" class="item">
                        <el-input v-model="product.payPrice" placeholder=""></el-input>
                    </el-form-item>
                    <!--  -->
                    <!-- <el-form-item label="">
                        <el-input v-model="product.origPrice" placeholder=""></el-input>
                    </el-form-item> -->
                    <el-form-item label="()" prop="orderCycleCount">
                        <el-input v-model="product.orderCycleCount" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="giftId">
                        <el-input v-model="product.giftId" placeholder="H11341"></el-input>
                    </el-form-item>
                </el-col>

                <el-col :span="12">
                    <el-form-item label="" class="item" prop="title">
                        <el-input v-model="product.title" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="payType">
                        <el-select v-model="product.payType" placeholder="">
                            <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="type">
                        <el-select v-model="product.type" placeholder="">
                            <el-option v-for="type in productTypeList" :label="type.text" :value="type.value" :key="type.value"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="" prop="minPoint">
                        <el-input v-model="product.minPoint" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" class="item" prop="payPoint">
                        <el-input v-model="product.payPoint" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="">
                <el-input v-model="product.exclusionPID" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input type="textarea" :rows="2" placeholder="" v-model="product.desc">
                </el-input>
            </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
            <span id="tishi">*ID
            </span>
            <el-button type="primary" size="mini" @click="doModifyProduct"></el-button>
        </span>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    name: "ModifyProduct",
    props: {
        product: {}
    },
    data() {
        var checkProdectCode = (rule, value, callback) => {
            if (!value) {
                return callback(new Error("ID"));
            }
            setTimeout(() => {
                if (isNaN(value) || value.substr(0, 1) != 1) {
                    callback(new Error("ID"));
                } else if (value.length > 20) {
                    callback(new Error("ID20"));
                } else {
                    this.$productRequest.getProductByCode({
                        productCode: this.product.productCode,
                        onResult(isSuccess, data) {
                            if (isSuccess && data.code == 200) {
                            } else {
                                callback(new Error("ID"));
                            }
                        }
                    });
                    callback();
                }
            }, 1000);
        };
        return {
            payTypeList: [],
            productTypeList: [],
            rules1: {
                productCode: [
                    {
                        required: true,
                        validator: checkProdectCode,
                        trigger: "blur"
                    }
                ],
                name: [
                    {
                        required: true,
                        message: "",
                        max: 20,
                        trigger: "blur"
                    }
                ],
                title: [
                    {
                        required: true,
                        message: "",
                        max: 20,
                        trigger: "blur"
                    }
                ],

                payType: [
                    {
                        required: true,
                        message: "",
                        trigger: "change",
                        pattern: /^[1-5]$/
                    }
                ],
                minPoint: [
                    {
                        required: false,
                        message: "",
                        trigger: "blur"
                    },
                    { pattern: /^[0-9]{0,}$/, message: "" }
                ],
                maxPoint: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur"
                    },
                    { pattern: /^[0-9]{0,}$/, message: "" }
                ],
                payPoint: [
                    {
                        required: false,
                        trigger: "blur",
                        pattern: /^[0-9]{0,}$/,
                        message: ""
                    }
                ],
                payPrice: [
                    {
                        required: false,
                        trigger: "blur",
                        pattern: /^[0-9]{0,}$/,
                        message: ""
                    }
                ],
                orderCycleCount: [
                    {
                        required: false,
                        trigger: "blur",
                        pattern: /^[0-9]{0,}$/,
                        message: ""
                    }
                ]
            }
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.product.showModifyProduct;
        }
    },
    created() {
        this.payTypeList = Constant.payTypeList;
        this.productTypeList = Constant.productTypeList;
    },
    methods: {
        handleClose() {
            this.$refs["modProduct"].resetFields();
            this.$store.commit("SET_SHOW_MODIFYPRODUCT", false);
        },
        doModifyProduct() {
            var that = this;
            this.$refs["modProduct"].validate(valid => {
                if (valid) {
                    this.$emit("updateProduct", this.product);
                } else {
                    that.$message({
                        type: "error",
                        message: ""
                    });
                    return false;
                }
            });
        }
    }
};
</script>
<style>
#tishi {
    float: left;
    color: rgb(50, 142, 234);
}
</style>




