<template>
    <div>
        <h3>
            <span style="color:red;font-weight:700;margin-right:6px;"></span>
        </h3>
        <el-form :inline="true" size="mini" :model="formInline" class="demo-form-inline">
            <el-row :gutter="20">
                <el-col :span="10">
                    <el-form-item label="ID:">
                        <el-input v-model="formInline.code" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="10">
                    <el-form-item label=":">
                        <el-input v-model="formInline.name" placeholder=""></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="4">
                    <el-form-item>
                        <el-button type="primary" style="margin-top:-5px;" @click="searchBaseProduct"></el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>

        <el-table :data="baseProductList" v-loading="isLoading" style="width: 100%">
            <el-table-column label="ID" align="center" show-overflow-tooltip>
                <template slot-scope="scope">
                    <span>{{ scope.row.code }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" show-overflow-tooltip="">
                <template slot-scope="scope">
                    <span>{{ scope.row.name}}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.point}}</span>
                </template>
            </el-table-column>
            <!---->
            <el-table-column label="" align="center" width="90">
                <template slot-scope="scope">
                    <span>{{scope.row.payType | payTypeFilter}}</span>
                </template>
            </el-table-column>
            <!--  -->
            <!---->
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span v-if="scope.row.type==0"></span>
                    <span v-if="scope.row.type==1"></span>
                </template>
            </el-table-column>
            <!---->
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span v-if="scope.row.status==1"></span>
                    <span v-if="scope.row.status==0"></span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <span>{{ scope.row.rentalPeriod }}</span>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="viewMutexProduct(scope.row.exclusionPID)"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="viewDesc(scope.row.desc)"></el-button>
                </template>
            </el-table-column>
            <!-- / -->
            <el-table-column label="" align="center">
                <template slot-scope="scope">
                    <el-button type="text" @click="viewDesc(scope.row.desc)"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="/" align="center">
                <template slot-scope="scope">
                    <el-switch v-model="scope.row.status" :active-value="1" :inactive-value="0" @change="statusChange(scope.row)">
                    </el-switch>
                </template>
            </el-table-column>
            <!-- <el-table-column label="/" align="center">
        <template slot-scope="scope">
          <el-switch v-model="scope.row.payType" :active-value="1" :inactive-value="0" @change="typeChange(scope.row)">
          </el-switch>
        </template>
      </el-table-column> -->
        </el-table>
        <div style="float:right;">
            <el-pagination class="layout-pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.number" :page-sizes="pageInfo.sizeArray" :page-size="pageInfo.currentSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.totalElements">
            </el-pagination>
        </div>
        <div style="clear:both;"></div>
    </div>
</template>

<script>
import Constant from "@/assets/js/Constant";
export default {
    components: {},
    props: {},
    data() {
        return {
            formInline: {
                code: "",
                name: ""
            },
            baseProductList: [
                {
                    code: "122233333333",
                    desc: "string",
                    exclusionPID: "string",
                    id: 0,
                    name: "string",
                    origPrice: 0,
                    payType: 0,
                    point: 0,
                    rentalPeriod: 0,
                    status: 0,
                    type: 0
                }
            ],
            pageInfo: {
                currentSize: 10,
                sizeArray: [10, 20, 50, 100],
                number: 1,
                totalElements: 0
            },
            isLoading: false
        };
    },

    created() {
        this.getBaseProductListFromWeb();
    },

    methods: {
        /**
         * 
         */
        getBaseProductListFromWeb() {
            this.isLoading = true;
            let that = this;
            this.$baseProductRequest.getBaseProductList({
                page: this.pageInfo.number - 1,
                size: this.pageInfo.currentSize,
                name: this.formInline.name,
                code: this.formInline.code,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.handleGetBaseProductListSuccessResponse(data);
                    } else {
                    }
                    that.isLoading = false;
                }
            });
        },
        handleGetBaseProductListSuccessResponse(data) {
            this.handlePageInfo(data);
            this.baseProductList = data.result;
        },
        /**
         * page 
         */
        handlePageInfo(data) {
            this.pageInfo.totalElements = data.page.totalElements;
            console.log(this.pageInfo);
        },
        /**
         * 
         */
        handleSizeChange(val) {
            console.log(` ${val} `);
            this.pageInfo.currentSize = val;
            this.getBaseProductListFromWeb();
        },
        /**
         * 
         */
        handleCurrentChange(val) {
            console.log(`: ${val}`);
            this.pageInfo.number = val;
            this.getBaseProductListFromWeb();
        },
        /**
         *  pageInfo
         */
        resetPageInfo() {
            this.pageInfo.currentSize = this.pageInfo.sizeArray[0];
            this.pageInfo.number = 1;
        },
        /**
         * 
         */
        searchBaseProduct() {
            this.resetPageInfo();
            this.getBaseProductListFromWeb();
        },
        /**
         * 
         */
        statusChange(baseProduct) {
            let that = this;
            this.$baseProductRequest.updateOnlineStatus({
                productId: baseProduct.id,
                onlineStatus: baseProduct.status,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        if (data.status == 200) {
                            that.$message({
                                type: "success",
                                message: ""
                            });
                        } else {
                            baseProduct.status =
                                baseProduct.status == 0 ? 1 : 0;
                            that.$message({
                                type: "error",
                                message: ""
                            });
                        }
                    } else {
                        baseProduct.status = baseProduct.status == 0 ? 1 : 0;
                        that.$message({
                            type: "error",
                            message: ""
                        });
                    }
                }
            });
        },
        /**
         * 
         */
        typeChange(baseProduct) {
            let that = this;
            console.log("");
        },
        /** */
        viewMutexProduct(exclusionPID) {
            this.$alert(
                "<div style='text-align:center;'>" + exclusionPID + "</div>",
                {
                    dangerouslyUseHTMLString: true
                }
            );
        },
        /** */
        viewDesc(desc) {
            this.$alert("<div style='text-align:center;'>" + desc + "</div>", {
                dangerouslyUseHTMLString: true
            });
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    },

    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



