<template>
    <el-dialog title="" :visible.sync="isCurrentShow" width="61.8%" :before-close="handleClose" :close-on-click-modal="false">
        <el-form size="mini" label-width="25%">
            <el-form-item label="">
                <el-input v-model="userId" placeholder=""></el-input>
                <el-button style="margin-left:15%;" @click="getUser"></el-button>
            </el-form-item>
        </el-form>
        <hr/>
        <el-form ref="order" size="mini" :model="subscriber" label-position="right" label-width="30%">
            <el-form-item label="*">
                {{subscriber.userId}}
            </el-form-item>
            <el-form-item label="*">
                {{subscriber.deviceId}}
            </el-form-item>
            <el-form-item label="*">
                {{subscriber.phone}}
            </el-form-item>
            <el-form-item label="*">
                {{subscriber.userName}}
            </el-form-item>
            <el-form-item label="*">
                <span style="color:red;">{{subscriber.point}}</span>
            </el-form-item>
            <el-form-item label="*">
                <el-radio-group v-model="marketingType">
                    <el-radio label="1"></el-radio>
                    <el-radio label="2"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item v-if="marketingType == 2" label="*">
                <el-select v-model="activity" placeholder="" style="width:50%;">
                    <el-option v-for="activity in activityList" :label="activity.name" :value="activity" :key="activity.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item v-if="marketingType == 1" label="*">
                <el-radio-group v-model="productType" @change="searchProductList">
                    <el-radio label="1"></el-radio>
                    <el-radio label="2"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item v-if="marketingType == 1" label="*">
                <el-select v-if="productType==1" v-model="subscriber.productCode" placeholder="" style="width:50%;">
                    <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                </el-select>
                <el-select v-if="productType==2" v-model="subscriber.productCode" placeholder="" style="width:50%;">
                    <el-option v-for="payType in payTypeList" :label="payType.text" :value="payType.value" :key="payType.value"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item v-if="marketingType == 1 && productType==1" label="*">
                <el-input-number v-model="cycleCount" :min="1" :max="10" style="width:50%;"></el-input-number>
            </el-form-item>
            <el-form-item label="*">
                <el-input v-model="orderPhone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item v-if="marketingType == 1" label="*">
                {{product.payType | payTypeFilter}}
            </el-form-item>
            <el-form-item>
                <el-button style="float:right; margin-right:15%; background-color:#009933; color:white; border:none;"></el-button>
            </el-form-item>
        </el-form>
    </el-dialog>
</template>
<script>
import Constant from "@/assets/js/Constant";
export default {
    props: {
        activityList: [] // 
    },
    data() {
        return {
            payTypeList: [],
            userId: "",
            marketingType: "2", // 1:; 2:
            productType: "1", //  1:; 2:
            cycleCount: "", // 
            productList: [], // 
            activity: {}, // 
            product: {}, // 
            orderPhone: "", // 

            subscriber: {
                address: "string",
                crmId: "string",
                deviceId: "string",
                phone: "string",
                point: 0,
                userId: "string",
                userName: "string",
                userType: 0
            }
        };
    },
    computed: {
        isCurrentShow() {
            return this.$store.state.orderAndRefund.showApplyOpenDlg;
        }
    },
    created() {
        this.payTypeList = Constant.payTypeList;
    },
    methods: {
        /** */
        searchProductList() {
            let that = this;
            this.$productRequest.getProductList({
                activityId: this.activity.id,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.productList = data.result;
                    } else {
                    }
                }
            });
        },
        /** */
        handleClose() {
            this.$refs["order"].resetFields();
            this.$store.commit("SET_SHOW_APPLYOPENDLG", false);
        },
        /** */
        getUser() {
            let that = this;
            this.$userManageRequest.searchSubscriber({
                userId: that.userId,
                onResult(isSuccess, data) {
                    if (isSuccess) {
                        that.subscriber = data.result;
                    } else {
                    }
                }
            });
        }
    },
    filters: {
        payTypeFilter(payType) {
            return Constant.payTypeMap[payType];
        }
    },
    watch: {}
};
</script>
<style scoped>
.el-input {
    width: 50%;
}

.el-form-item {
    color: #929292;
}
</style>





