                <td>{{activity.payType}}</td>
            </tr>
            <tr>
                <td class="colum"></td>
                <td>{{activity.deviceId}}</td>
            </tr>
            <tr>
                <td class="colum"></td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr>
