                <td class="colum"></td>
                <td>{{activity.payType}}</td>
            </tr>
            <tr>
                <td class="colum"></td>
                <td>{{activity.deviceId}}</td>
            </tr>
            <tr>
                <td class="colum"></td>
                <td>{{activity.validTime}}</td>
            </tr>
            <tr>
                <td class="colum"></td>
                <td>{{activity.payType}}</td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    props: {},
    data() {
        return {
            activity: {}
        };
    },
    created() {},
    methods: {},
    filters: {},
    watch: {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
td {
    text-align: center;
    border: 1px solid #cccccc;
    height: auto;
}
.colum {
    padding: 6px;
    font: 1.2em sans-serif;
    font-weight: 500;
    color: black;
    width: 40%;
}
.tree {
    width: 90%;
    margin: auto;
}
</style>



