<template>
    <div class="main-container gray-bg">
        <el-form ref="setting" :model="form" label-width="30%" size="mini" label-position="right">
            <el-form-item label="">
                <el-input v-model="form.maxSubscriber" placeholder=""></el-input>
                <span>200</span>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.maxCallCount" placeholder=""></el-input>
                <span>5</span>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.pointRefreshInterval" placeholder=""></el-input>
                <span>5</span>
            </el-form-item>
            <el-form-item label="-">
                <el-radio-group v-model="form.pointSearch">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
                <span>(  )</span>
            </el-form-item>
            <el-form-item label="18:30">
                <el-radio-group v-model="form.sendMsgSwitch">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="6:30">
                <el-radio-group v-model="form.filterOrderSwitch">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="" v-if="form.filterOrderSwitch==1">
                <el-input v-model="form.name" placeholder=""></el-input>
                <span>0</span>
            </el-form-item>

            <el-form-item label="" v-if="form.filterOrderSwitch==1">
                <el-input v-model="form.name" placeholder=""></el-input>
                <span>24</span>
            </el-form-item>
            <el-form-item label="18:00,21:00">
                <el-radio-group v-model="form.resource">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="form.name" placeholder=""></el-input>
                <span>60</span>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="form.resource">
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                    <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="form.resource">
                    <el-radio label=""></el-radio>
                    <el-radio label="sp"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="onSubmit"></el-button>
                <el-button></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            form: {}
        };
    },
    methods: {
        onSubmit() {
            // 
        }
    }
};
</script>
 <style lang="scss" scoped>
.el-input {
    width: 20%;
}
</style>



