import router from './router'
import store from '@/store/store'
import {
    Message
} from 'element-ui'

// permissiom judge
function hasPermission(roles, permissionRoles) {
    if (roles.indexOf('admin') >= 0) return true // admin 
    if (!permissionRoles) return true
    return roles.some(role => permissionRoles.indexOf(role) >= 0)
}

const whiteList = ['/login'] // 

router.beforeEach((to, from, next) => {
    // NProgress.start() // Progress
    if (true) { // token  true !!!!
        if (to.path === '/login') {
            // next({ path: '/' })
            next();
            // NProgress.done() // routerhash hash  afterEach hack pshistory
        } else {
            if (store.state.user.roles.length === 0) { // user_info
                store.dispatch('GetUserInfo').then(res => { // user_info
                    const roles = res.data.role
                    store.dispatch('GenerateRoutes', {
                        roles
                    }).then(() => { // 
                        router.addRoutes(store.state.permission.addRouters) // 
                        next({ ...to,
                            replace: true
                        }) // hack addRoutes ,replace: true so the navigation will not leave a history record
                        // router.replace(to);
                    })
                }).catch((e) => {
                    console.log(e);
                    store.dispatch('FedLogOut').then(() => {
                        Message.error(',')
                        next({
                            path: '/login'
                        })
                    })
                })
            } else {
                // next()  
                if (hasPermission(store.state.user.roles, to.meta.role)) {
                    next() //
                } else {
                    next({
                        path: '/401',
                        query: {
                            noGoBack: true
                        }
                    })
                    // NProgress.done() // routerhash hash  afterEach hack pshistory
                }
                //  
            }
        }
    } else {
        if (whiteList.indexOf(to.path) !== -1) { // 
            next()
        } else {
            next('/login') // 
            // NProgress.done() // routerhash hash  afterEach hack pshistory
        }
    }
})

router.afterEach(() => {
    // NProgress.done() // Progress
})

